.coffee{
    display: flex;
    background: #fff;
    margin: 30px;
    border-radius: 10px;
    text-align: left;
}
.coffeecup{
    padding: 20px;
    border-radius: 10px;
}
.coffee__info{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    font-family: 'Bitter';
    font-style: italic;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #313237;
    padding: 20px;
}
.review{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
}
.review__text{
    display: flex;
    flex-direction: column;
}
.review__text h2{
    font-family: 'InterThin';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 26px;
    color: #313237;
    margin-block-start: 0px;
    margin-block-end: 0px;
}
.review__text p{
    margin-block-start: 0em;
    margin-block-end: 0em;
    font-family: 'InterThin';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    color: #718797;
}
.review img{
    width: 100px;
    height: 20px;
}