.card{
    text-align: left;
    display: flex;
    flex-direction: row;
    background-color: #fff;
    margin: 30px;
    border-radius: 10px;
}
.card .pic{
    border-radius: 10px;
    padding: 20px;
}
.card__info{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 2rem;
    margin-right: 1rem;
}
h1{
    font-family: 'Amiko';
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    line-height: 34px;
    color: #313237;
}
.card__info-main p{
    font-family: 'InterThin';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: #718797;
}