.card__author{
    display: flex;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    padding-right: 2rem;
    padding-bottom: 20px;
}
.card__author_info_full h2{
    text-align: left;
    font-family: 'InterThin';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 26px;
    color: #313237;
    line-height: 26px;
    margin-left: 7px;
}
.card__author_info_full p{
    margin-left: 7px;
    text-align: left;
    font-family: 'InterThin';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    color: #718797;
    line-height: 5px;
}
.card__author_info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
}
.arrow{
    width: 20px;
    height: 17.5px;
    padding-right: 35px;
    cursor: pointer;
}
card__author_info_full{
    display: flex;
    flex-direction: column;
}