.App {
  text-align: center;
  justify-content: center;
  max-width: 1540px;
  background-color: #E5E5E5;
  margin: 30px;
}
.blog_1{
  display: flex;
  flex-direction: row;
}
.blog_2{
  display: flex;
  flex-direction: row;
}
.blog_3 .card{
  flex-direction: column;
}
.blog_3 .pic{
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 20px;
}
.blog_3{
  display: flex;
  flex-direction: column;
}
.blog_4{
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 0.5fr ;
}
.blog_5{
  display: flex;
  flex-direction: row;
}
.blog_6{
  display: flex;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  background-color: #E5E5E5;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

@media (max-width: 1500px){
  .blog_1{
    display: flex;
    flex-direction: column;
  }
  .blog_2{
    display: flex;
    flex-direction: column;
  }
  .blog_3 .card{
    flex-direction: column;
  }
  .blog_3 .pic{
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 20px;
  }
  .blog_3{
    display: flex;
    flex-direction: column;
  }
  .blog_4{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 0.5fr ;
  }
  .blog_5{
    display: flex;
    flex-direction: column;
  }
  .blog_5 .mapblock{
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .blog_6{
    display: flex;
  }
  .coffee{
    flex-direction: column;
  }
  .lampblock{
    flex-direction: column;
  }
  .blog_1 .card{
    flex-direction: column;
  }
  .App {
    margin: 30px 0;
  }

  .feature3{
    justify-content: center;
  }
}
@media (max-width: 635px) {
  .feature3{
    justify-content: space-between;
  }
  .pic{
    width: 90%;
  }
  .card{
    align-items: center;
  }
  .coffeecup{
    width: 90%;
  }
  .map{
    width: 90%;
  }
  .lamp{
    width: 90%;
  }
}