.blockfeatures{
    text-align: left;
    justify-content: space-between;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    margin: 0 30px;
    padding: 10px 0;
}
.feature2{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 20px;
}
.feature{
    margin: 20px 0;
    background: #fff;
    border-radius: 10px;
}
.feature2 span{
    height: 0;
    width: 20%;
    border: 1px solid #5374FF;
}
.feature2 span:nth-child(4){
    height: 0;
    width: 20%;
    border: 1px solid #B6C7D4;
}
.circle{
    font-family: 'InterThin';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: #718797;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background: #EDEDF2;
}
.circle:first-child{
    background: #5374FF;
    color: #fff;
}
.feature3{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.feature3 img{
    width: 196px;
    height: 60px;
    margin-left: 10px;
}
.feature3 h2{
    font-family: 'InterThin';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 26px;
    color: #313237;
}
.dreamers{
    margin-left: 10px;
    font-family: 'InterThin';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    color: #718797;
}
.feature1{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.searchbar{
    display: flex;
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
}
.searchbar input{
    border: none;
    width: 100%;
    text-align: left;
    padding-left: 20px;
    background: #EDEDF2;
    border-radius: 10px 0 0 10px;
    font-family: 'InterThin';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: #718797;
    outline: none;
}
.searchbar button{
    border: none;
    background: #5374FF;
    border-radius: 0 10px 10px 0;
    width: 60px;
    height: 53px;
}
.feature1 p{
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    font-family: 'InterThin';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: #718797;
}
@media (max-width: 1600px) {
    .feature3 {
        justify-content: center;
    }
}
@media (max-width: 635px) {
    .feature3 {
        justify-content: space-between;
    }
}