.mapblock{
    background-color: #fff;
    text-align: center;
    justify-content: center;
    border-radius: 10px;
    margin: 30px;
}
.mapblock .map{
    padding: 15px;
}
.mapblock__info-geo p{
    font-family: 'InterThin';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: #313237;
    text-wrap: normal;
    padding: 0 30px;
}
.mapblock__info-geo span{
    font-family: 'InterThin';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 26px;
    color: #5374FF;
}
.p1{
    font-family: 'InterThin';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    color: #718797;
    align-items: center;
    padding-left: 30px;
}
.p1 img{
    margin-right: 20px;
}