@font-face {
  font-family: "InterThin";
  src: url("./fonts/InterThin/InterThin.ttf") format("truetype");
  font-style: normal;
  font-weight: 100;
}
@font-face {
  font-family: "InterThin";
  src: url("./fonts/InterRegular/InterRegular.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: 'Amiko';
  src: local('Amiko'), local('Amiko-Regular'), url('./fonts/Amiko.woff2') format('woff2'), url('./fonts/Amiko.woff') format('woff'), url('./fonts/Amiko.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Amiko';
  src: local('Amiko SemiBold'), local('Amiko-SemiBold'), url('./fonts/Amikosemibold.woff2') format('woff2'), url('./fonts/Amikosemibold.woff') format('woff'), url('./fonts/Amikosemibold.ttf') format('truetype');
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: 'Amiko';
  src: local('Amiko Bold'), local('Amiko-Bold'), url('./fonts/Amikobold.woff2') format('woff2'), url('./fonts/Amikobold.woff') format('woff'), url('./fonts/Amikobold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'Bitter';
  src: local('Bitter Italic'), local('Bitter-Italic'), url('./fonts/Bitteritalic.woff2') format('woff2'), url('./fonts/Bitteritalic.woff') format('woff'), url('./fonts/Bitteritalic.ttf') format('truetype');
  font-weight: 400;
  font-style: italic;
}
@font-face {
  font-family: 'Bitter';
  src: local('Bitter'), local('Bitter-Regular'), url('./fonts/Bitter.woff2') format('woff2'), url('./fonts/Bitter.woff') format('woff'), url('./fonts/Bitter.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Bitter';
  src: local('Bitter Bold Italic'), local('Bitter-BoldItalic'), url('./fonts/Bitterbolditalic.woff2') format('woff2'), url('./fonts/Bitterbolditalic.woff') format('woff'), url('./fonts/Bitterbolditalic.ttf') format('truetype');
  font-weight: 700;
  font-style: italic;
}
@font-face {
  font-family: 'Bitter';
  src: local('Bitter Bold'), local('Bitter-Bold'), url('./fonts/Bitterbold.woff2') format('woff2'), url('./fonts/Bitterbold.woff') format('woff'), url('./fonts/Bitterbold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
  background-color: #E5E5E5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
