.lampblock{
    display: flex;
    background-color: #fff;
    text-align: left;
    margin: 30px;
    border-radius: 10px;
}
.lamp__info_up{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 10px 20px;
    font-family: 'InterThin';
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 17px;
    color: #718797;
}
.lamp__info_down{
    display: flex;
    padding: 20px 20px;
    flex-direction: column;

}
.lamp__info_up img{
    cursor: pointer;
    transform: rotate(90deg);
}
.lampblock img{
    padding: 20px;
}
.lamp__info_text p{
    font-family: 'InterThin';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: #718797;
}
.lamp__info_text h1{
    font-family: 'InterThin';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 26px;
    color: #313237;
}
.lamp__info_icons{
    display: flex;
    width: 200px;
    height: 40px;
    justify-content:space-between;
}
.lamp__info_icons img{
    padding: 10px 10px;
    cursor: pointer;
}
.lamp__info{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}